                dark
                @click="$store.commit('PREV_TAB')"
              >
                
              </v-btn>
            </v-col>
          </v-row>
        </v-stepper-content>
        <v-stepper-content step="6">
          <total></total>
          <v-row>
            <v-col>
              <v-btn
                color="success"
                dark
                @click="$store.commit('PREV_TAB')"
              >
                
              </v-btn>
