<script>
  import { mapMutations } from 'vuex'
  import Guests from '../components/Guests'
  import Total from '../components/Total'
  export default {
    components: {
      Guests,
      Total,
    },
    data () {
      return {
        discount: '',
        discountType: null,
        whatToDo: '',
        desc: '',
        types: [
          { text: '%', value: 'perc' },
          { text: '.', value: 'fix' },
        ],
        booking: {
          reservSource: '',
          reservNumber: '',
          earlyIn: false,
          latelyOut: false,
          sleepingArea: false,
          sleepingAreaCount: [1, 2, 3, 4, 5, 6, 7, 8, 9],
          priceType: '',
          status: '',
          noteType: '',
        },
        noteType: [
          { text: ' ', value: 'undefined' },
          { text: '', value: 'accepted' },
          { text: '', value: 'cancelled' },
        ],
        status: [
          { text: ' ', value: 'undefined' },
          { text: '', value: 'accepted' },
          { text: '', value: 'canceled' },
          { text: '', value: 'out' },
