    },
    watch: {
      task: function () {
        if (this.task === 'task1') {
          this.whatToDo = '    '
          this.desc = ''
        } else if (this.task === 'task2') {
          this.whatToDo = '   '
          this.desc = ''
        } else {
          this.whatToDo = '   '
          this.desc = ' :   \n' +
            ': \n' +
            '- : NaN\n' +
            ': 17-08-2020 13:00'
        }
      },
    },
    created () {
    },
    methods: {
      ...mapMutations({
        setBookingData: 'SET_BOOKING_DATA',
        reserve: 'SET_RESERVE',
        setPopulate: 'SET_POPULATE',
        annulate: 'DELETE',
        displayNone: 'DISPLAY_NONE',
      }),
      reserveValidate () {
        if (!this.$refs.form.validate()) {
          this.$store.commit('TO_TAB', 2)
        } else {
          var obj = {
            firstName: this.firstName,
            lastName: this.lastName,
            patronymic: this.patronymic,
            passport: this.passport,
            phone: this.phone,
            email: this.email,
          }
          this.$store.commit('SET_RESERVE', obj)
        }
      },
      bookingValidate () {
        console.log(this.$store.state.status)
        if (!this.$refs.form.validate()) {
          this.$store.commit('TO_TAB', 2)
        } else {
          this.setBookingData()
        }
      },
      async bookingAnnulate () {
        await this.displayNone()
        await this.annulate()
      },
    },
    computed: {
      price: function () {
        const days = this.days
        const categoryPrice = this.categoryPrice
        return days * categoryPrice
      },
      days: function () {
        const unixDays = new Date(Date.parse(this.$store.state.end) - Date.parse(this.$store.state.start))
        return unixDays.getTime() / 60 / 60 / 24 / 1000 + 1
      },
      categoryPrice: function () {
        let categoryPrice = 4500
        if (this.$store.state.category === 'lux') {
          categoryPrice = 10000
        } else if (this.$store.state.category === 'halfLux') {
          categoryPrice = 6500
        }
        return categoryPrice
      },
      servicesPrice: function () {
        return this.$store.state.services.lunch * this.$store.state.servicesCount.lunch + this.$store.state.services.additionalSeats * this.$store.state.servicesCount.additionalSeats + this.$store.state.services.rentalBike * this.$store.state.servicesCount.rentalBike
      },
      discounts: function () {
        let discount = 0
        if (this.discountType === 'perc') {
          discount = (this.price * this.discount) / 100
        } else {
          discount = this.discount
        }
        return discount
      },
      lastName: {
        get: function () {
          return this.$store.state.client.lastName
        },
        set: function (value) {
          this.$store.commit('UPDATE_LAST_NAME', value)
        },
      },
      firstName: {
        get: function () {
          return this.$store.state.client.firstName
        },
        set: function (value) {
          this.$store.commit('UPDATE_FIRST_NAME', value)
        },
      },
      patronymic: {
        get: function () {
          return this.$store.state.client.patronymic
        },
        set: function (value) {
          this.$store.commit('UPDATE_PATRONYMIC', value)
        },
      },
      passport: {
        get: function () {
          return this.$store.state.client.passport
        },
        set: function (value) {
          this.$store.commit('UPDATE_PASSPORT', value)
        },
      },
      phone: {
        get: function () {
          return this.$store.state.client.phone
        },
        set: function (value) {
          this.$store.commit('UPDATE_PHONE', value)
        },
      },
      email: {
        get: function () {
          return this.$store.state.client.email
        },
        set: function (value) {
          this.$store.commit('UPDATE_EMAIL', value)
        },
      },
      commentary: {
        get: function () {
          return this.$store.state.client.commentary
        },
        set: function (value) {
          this.$store.commit('UPDATE_COMMENTARY', value)
        },
      },
    },
  }
