<template>
  <v-row>
    <v-col
      cols="12"
      class="orange"
    >
      <strong class="ml-3 title">  </strong>
    </v-col>
    <v-col
      cols="4"
      class="pt-1 pb-1"
    ><span class="ml-3"> </span></v-col>
    <v-col
      cols="8"
      class="pt-1 pb-1"
    >{{ reservSource[$store.state.reservSource] }}</v-col>
    <v-col
      cols="4"
      class="pt-1 pb-1"
    ><span class="ml-3"> </span></v-col>
    <v-col
      cols="8"
      class="pt-1 pb-1"
    >{{ $store.state.room }}</v-col>
    <v-col
      cols="4"
      class="pt-1 pb-1"
    ><span class="ml-3"></span></v-col>
    <v-col
      cols="8"
      class="pt-1 pb-1"
    >{{ $store.state.start }} - {{ $store.state.end }}</v-col>
    <v-col
      cols="4"
      class="pt-1 pb-1"
    ><span class="ml-3"></span></v-col>
    <v-col
      cols="8"
      class="pt-1 pb-1"
    >{{ roomCategory[$store.state.category] }}</v-col>
    <v-col
      cols="4"
      class="pt-1 pb-1"
    ><span class="ml-3"></span></v-col>
    <v-col
      cols="8"
      class="pt-1 pb-1"
    >{{ $store.state.room }}</v-col>
    <v-col
      cols="4"
      class="pt-1 pb-1"
    ><span class="ml-3"> </span></v-col>
    <v-col
      cols="8"
      class="pt-1 pb-1"
    >{{ $store.state.earlyIn?'':'' }}</v-col>
    <v-col
      cols="4"
      class="pt-1 pb-1"
    ><span class="ml-3"> </span></v-col>
    <v-col
      cols="8"
      class="pt-1 pb-1"
    >{{ $store.state.latelyOut?'':'' }}</v-col>
    <v-col
      cols="4"
      class="pt-1 pb-1"
    ><span class="ml-3"></span></v-col>
    <v-col
      cols="8"
      class="pt-1 pb-1"
    ></v-col>
    <v-col
      cols="4"
      class="pt-1 pb-1"
    ><span class="ml-3">. </span></v-col>
    <v-col
      cols="8"
      class="pt-1 pb-1"
    >{{ $store.state.servicesCount.additionalSeats * $store.state.services.additionalSeats }}</v-col>
    <v-col
      cols="4"
      class="pt-1 pb-1"
    ><span class="ml-3"></span></v-col>
    <v-col
      cols="8"
      class="pt-1 pb-1"
    >{{ tasks[$store.state.task] }}</v-col>
    <v-col
      cols="12"
      class="orange"
    >
      <strong class="ml-3 title"> </strong>
    </v-col>
    <v-col
      cols="4"
      class="pt-1 pb-1"
    ><span class="ml-3">:</span></v-col>
    <v-col
      cols="8"
      class="pt-1 pb-1"
    >{{ $store.state.client.lastName }} {{ $store.state.client.firstName }}</v-col>
    <v-col
      cols="4"
      class="pt-1 pb-1"
    ><span class="ml-3">:</span></v-col>
    <v-col
      cols="8"
      class="pt-1 pb-1"
    >{{ $store.state.client.phone }}</v-col>
    <v-col
      cols="4"
      class="pt-1 pb-1"
    ><span class="ml-3">E-mail:</span></v-col>
    <v-col
      cols="8"
      class="pt-1 pb-1"
    >{{ $store.state.client.email }}</v-col>
    <v-col
      cols="4"
      class="pt-1 pb-1"
    ><span class="ml-3">:</span></v-col>
    <v-col
      cols="8"
      class="pt-1 pb-1"
    >{{ $store.state.client.passport }}</v-col>
    <v-col
      cols="12"
      class="orange"
    >
      <strong class="ml-3 title"></strong>
    </v-col>
    <v-col
      cols="4"
      class="pt-1 pb-1"
    ><span class="ml-3"></span></v-col>
    <v-col
      cols="8"
      class="pt-1 pb-1"
    >{{ categoryPrice }}</v-col>
    <v-col
      cols="4"
      class="pt-1 pb-1"
    ><span class="ml-3"></span></v-col>
    <v-col
      cols="8"
      class="pt-1 pb-1"
    >{{ days }}</v-col>
  </v-row>
</template>

<script>
  export default {
    name: 'Total',
    data () {
      return {
        reservSource: {
          directSale: ' ',
          completeService: 'Complete Service',
          algri: 'Algri travel',
          go2trip: 'Go2trip',
        },
        roomCategory: {
          lux: '',
          halfLux: '',
          std: '',
        },
        tasks: {
          task1: '     ',
          task2: '    ',
          task3: '   ',
        },
      }
    },
    computed: {
      categoryPrice: function () {
        let categoryPrice = 4500
        if (this.$store.state.category === 'lux') {
          categoryPrice = 10000
        } else if (this.$store.state.category === 'halfLux') {
          categoryPrice = 6500
        }
        return categoryPrice
      },
      days: function () {
        const unixDays = new Date(Date.parse(this.$store.state.end) - Date.parse(this.$store.state.start))
        return unixDays.getTime() / 60 / 60 / 24 / 1000 + 1
      },
    },
  }
</script>

<style scoped>
  .orange {
    background: orange;
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }
  .title {
    font-size: 16px !important;
    font-weight: bold;
  }
</style>
